import axios from 'axios';
const url = 'http://127.0.0.1:8888'
// 获取所有员工
export const getAll = () => {
  return axios.get(url + '/allentry')
}
// 录入员工信息
export const postEntry = (entryStaff) => {
  return axios.post(url + '/entrystaff', { ...entryStaff })
}
// 删除员工
export const delEntry = (_id) => {
  return axios.post(url + '/delentry', { _id })
}
// 用户登录
export const adminLogin = (admin) => {
  return axios.post(url + '/adminlogin', { ...admin })
}