<template>
  <div class="login-container">
    <Canvas />
    <el-form ref="loginForm"
             :model="loginForm"
             class="login-form"
             auto-complete="on"
             label-position="left">

      <div class="title-container">
        <h3 class="title">欢迎</h3>
      </div>

      <el-form-item prop="username">
        <span class="svg-container">
          用户名
        </span>
        <div class="el-input">
          <input type="text"
                 class="el-input__inner"
                 v-model="loginForm.username">
        </div>
      </el-form-item>

      <el-form-item prop="password">
        <span class="svg-container">
          密码
        </span>
        <div class="el-input">
          <input type="password"
                 class="el-input__inner"
                 v-model="loginForm.password">
        </div>
      </el-form-item>
      <div class="btn_from">
        <el-button type="primary"
                   style="width:45%;margin-bottom:30px;"
                   @click.native.prevent="handleLogin">登录</el-button>
        <el-button style="width:45%;margin-bottom:30px;"
                   @click="handleRegister">注册</el-button>
      </div>
    </el-form>
  </div>
</template>

<script>
import Canvas from "@/views/pc/canvas";
import { adminLogin } from "@/api/api.js";
export default {
  name: "Login",
  components: {
    Canvas
  },
  data() {
    return {
      // 登陆框中的默认值
      loginForm: {
        username: "admin",
        password: "123456"
      }
    };
  },
  methods: {
    // 登录按钮
    handleLogin() {
      function setCookie(name, value) {
        if (value) {
          var days = 1; //定义一天
          var exp = new Date();
          exp.setTime(exp.getTime() + days * 24 * 60 * 60 * 1000);
          // 写入Cookie, toGMTString将时间转换成字符串
          document.cookie =
            name + "=" + escape(value) + ";expires=" + exp.toGMTString;
        }
      }
      console.log(this.loginForm);
      adminLogin(this.loginForm).then(data => {
        if (data.data.code === 20000) {
          this.$router.push("/p_home");
          setCookie("login", "true");
        } else {
          this.$message({
            showClose: true,
            message: "密码或账号错误",
            type: "warning"
          });
        }
      });
    },
    // 注册按钮
    handleRegister() {
      this.$refs.loginForm.validate(valid => {
        if (valid) {
          this.$router.push({
            path: "/register"
          });
        }
      });
    }
  }
};
</script>

<style lang="scss">
/* 修复input 背景不协调 和光标变色 */
/* Detail see https://github.com/PanJiaChen/vue-element-admin/pull/927 */

$bg: #283443;
$light_gray: #fff;
$cursor: #fff;

@supports (-webkit-mask: none) and (not (cater-color: $cursor)) {
  .login-container .el-input input {
    color: $cursor;
  }
}

/* reset element-ui css */
.login-container {
  .el-input {
    display: inline-block;
    height: 47px;
    width: 85%;

    input {
      background: transparent;
      border: 0px;
      -webkit-appearance: none;
      border-radius: 0px;
      padding: 12px 5px 12px 15px;
      color: $light_gray;
      height: 47px;
      caret-color: $cursor;

      &:-webkit-autofill {
        box-shadow: 0 0 0px 1000px $bg inset !important;
        -webkit-text-fill-color: $cursor !important;
      }
    }
  }

  .el-form-item {
    border: 1px solid rgba(255, 255, 255, 0.1);
    background: rgba(0, 0, 0, 0.1);
    border-radius: 5px;
    color: #454545;
  }
}
</style>

<style lang="scss" scoped>
$bg: #2d3a4b;
$dark_gray: #889aa4;
$light_gray: #eee;

.login-container {
  min-height: 100%;
  width: 100%;
  background-color: $bg;
  overflow: hidden;

  .login-form {
    position: relative;
    width: 520px;
    max-width: 100%;
    padding: 160px 35px 0;
    margin: 0 auto;
    overflow: hidden;
  }

  .tips {
    font-size: 14px;
    color: #fff;
    margin-bottom: 10px;

    span {
      &:first-of-type {
        margin-right: 16px;
      }
    }
  }

  .svg-container {
    padding: 6px 5px 6px 15px;
    color: $dark_gray;
    vertical-align: middle;
    width: 50px;
    display: inline-block;
  }

  .title-container {
    position: relative;

    .title {
      font-size: 26px;
      color: $light_gray;
      margin: 0px auto 40px auto;
      text-align: center;
      font-weight: bold;
    }
  }

  .show-pwd {
    position: absolute;
    right: 10px;
    top: 7px;
    font-size: 16px;
    color: $dark_gray;
    cursor: pointer;
    user-select: none;
  }
  .btn_from {
    display: flex;
    justify-content: space-between;
  }
}
</style>
