<template>
  <div id="bodyBox">
    <el-container style="height: 100%;overflow:hidden; border: 1px solid #eee">
      <el-aside width="200px"
                style="background-color: rgb(238, 241, 246)">
        <el-menu>
          <router-link to="/p_home">
            <el-menu-item>员工列表</el-menu-item>
          </router-link>
          <router-link to="/p_entry">
            <el-menu-item>员工录入</el-menu-item>
          </router-link>
        </el-menu>
      </el-aside>
      <el-container>
        <el-header style="text-align: conter; font-size: 30px">
          <span>员工信息</span>
        </el-header>
        <el-main>
          <el-table :data="tableData">
            <el-table-column prop="username"
                             width="100px"
                             label="姓名"></el-table-column>
            <el-table-column prop="sex"
                             width="100px"
                             label="性别"></el-table-column>
            <el-table-column prop="age"
                             width="100px"
                             label="年龄"></el-table-column>
            <el-table-column prop="department"
                             width="180px"
                             label="部门 "></el-table-column>
            <el-table-column prop="address"
                             width="240px"
                             label="居住地"></el-table-column>
            <el-table-column prop="health"
                             label="是否健康"></el-table-column>
            <template>
              <el-table-column label="操作">
                <template slot-scope="scope">
                  <el-button type="danger"
                             @click="deletestu(scope.$index)">删除</el-button>
                </template>
              </el-table-column>
            </template>
          </el-table>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
import { getAll, delEntry } from "@/api/api.js";
export default {
  data() {
    return {
      tableData: []
    };
  },
  created() {
    getAll().then(data => {
      this.tableData = data.data;
    });
  },
  mounted() {
    let menuHeight = document.getElementById("bodyBox");
    let screenHeight = window.innerHeight; //浏览器窗口的内部高度
    menuHeight.style.height = screenHeight + "px";
  },
  methods: {
    deletestu(index) {
      delEntry(this.tableData[index]._id).then(doc => {
        this.$message({
          showClose: true,
          message: doc.data,
          type: "warning"
        });
        if(doc.data=='删除成功'){
          this.tableData.splice(index, 1);
        }
      });
    }
  }
};
</script>
<style scoped>
.el-header {
  background-color: #b3c0d1;
  color: #333;
  line-height: 60px;
}

.el-aside {
  color: #333;
}
a {
  text-decoration: none;
}
</style>
