import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/pc";
import Login from "../views/pc/login.vue";

Vue.use(VueRouter);
const routes = [
  {
    path: "/p_home",
    name: "p_home",
    component: Home,
    meta: {
      title: "首页"
    }
  },
  {
    path: "/p_login",
    name: "p_login",
    component: Login,
    meta: {
      title: "登录"
    }
  },
  {
    path: "/p_entry",
    name: "p_entry",
    meta: {
      title: "录入"
    },
    component: () => import("../views/pc/entry.vue")
  },
  {
    path: "/m_home",
    name: "m_home",
    meta: {
      title: "移动端"
    },
    component: () => import("../views/mobile")
  },
  {
    path: "/m_entry",
    name: "m_entry",
    meta: {
      title: "移动端"
    },
    component: () => import("../views/mobile/entry.vue")
  }
];

const router = new VueRouter({
  mode: "history",
  routes
});
function isMobile() {
  var userAgenInfo = navigator.userAgent;
  var Agents = new Array("Android", "iPhone", "SymbianOS", "iPad", "iPod");
  var flag = false;
  for (var v = 0; v < Agents.length; v++) {
    if (userAgenInfo.indexOf(Agents[v]) > 0) {
      flag = true;
      break;
    }
  }
  return flag;
}
router.beforeEach((to, from, next) => {
  if (isMobile()) {
    if (to.path === '/m_home' || to.path === '/m_entry') {
      next();
    } else {
      next('/m_home');
    }
  } else {
    if (to.path === '/p_home' || to.path === '/p_entry'|| to.path === '/p_login' ) {
      next()
    } else {
      next('/p_home');
    }
  }
});

export default router;
